import React, { useState } from "react";
import "./App.scss";
import { Box, Container, createStyles, makeStyles } from "@material-ui/core";
import { Header } from "./pages/containers/header";
import { useAppDispatch, useAppSelector } from "./store/hooks";
import { fetchNews } from "./pages/containers/ListoOfCards/listOfCardsSlice";

import { Pages } from "./pages";
import { CardSkeleton } from "./pages/containers/ListoOfCards/CardsSkeleton";

const useStyles = makeStyles(() =>
  createStyles({
    root: {
      marginTop: "65px",
    },
  }),
);
function App() {
  const classes = useStyles();
  const dispatch = useAppDispatch();
  const news = useAppSelector((state) => state.news);
  const routState = useAppSelector((state) => state.header.selectedTab);
  const [searchValue, setSearch] = useState("");
  if (news.state === "Initial") {
    dispatch(fetchNews());
  }

  return (
    <Container>
      <Box className={classes.root}>
        <Header onSearch={setSearch} />
        <Box mt={3}>
          {news.state === "Loading" && <CardSkeleton />}
          {news.state === "Loaded" && <Pages currentPage={routState} loadedState={news} searchValue={searchValue} />}
        </Box>
      </Box>
    </Container>
  );
}

export default App;
