import React, { FC } from "react";
import { LitOfCards } from "../containers/ListoOfCards";
import { NewsEntity } from "../../types";
import { getElementsByPage } from "../containers/ListoOfCards/utils";
import { Box } from "@material-ui/core";
import { NewsPagination } from "../containers/pagination";

export const News: FC<{
  news: Array<NewsEntity>;
  onBookmark: (newsId: number) => void;
  currentPage: number;
  handleNextPage: (nexPage: number) => void;
  lastNews: NewsEntity;
}> = ({ news, onBookmark, currentPage, handleNextPage, lastNews }) => {
  return (
    <Box>
      <LitOfCards cards={getElementsByPage(news, currentPage, 6)} lastNews={lastNews} onBookmark={onBookmark} />
      <Box mt={3}>
        {news.length > 0 && (
          <NewsPagination
            from={currentPage === 1 ? 1 : (currentPage - 1) * 6}
            to={currentPage * 6}
            count={news.length}
            onNext={() => handleNextPage(currentPage + 1)}
            onPrev={() => handleNextPage(currentPage - 1)}
          />
        )}
      </Box>
    </Box>
  );
};
