import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { NewsEntity, NewsResponse } from "../../../types";
import axios from "axios";

import { mock } from "../../../types/mock";
import { TabValues } from "../header/types";
import { prop } from "ramda";

export type NewsState =
  | {
      state: "Initial";
    }
  | {
      state: "Loading";
    }
  | {
      state: "Error";
      error: string;
    }
  | {
      state: "Loaded";
      news: Array<NewsEntity>;
      newsPage: number;
      bookmarksPage: number;
    };
const initialState: NewsState = {
  state: "Initial",
} as NewsState;
export const fetchNews = createAsyncThunk("news/fetch", () =>
  Promise.race([
    axios
      .get<Array<NewsResponse>>(
        "https://finnhub.io/api/v1/company-news?symbol=AAPL&from=2021-03-01&to=2021-03-15&token=bpjsf67rh5r9328ecgvg",
      )
      .then(prop("data"))
      //Just delay for demonstrate skeleton
      .then(
        (response) => new Promise<Array<NewsResponse>>((resolve) => setTimeout(() => resolve(response), 2000)),
      )
      .then((res) => res),
    //if API again endless pending....
    new Promise<Array<NewsResponse>>((res) => {
      setTimeout(() => {
        res(mock);
      }, 4000);
    }),
  ]),
);

export const newsSlice = createSlice({
  name: "news",
  initialState,
  reducers: {
    changePage(state, { payload }: PayloadAction<{ nextPage: number; list: TabValues }>) {
      if (state.state === "Loaded") {
        switch (payload.list) {
          case "bookmarks":
            state.bookmarksPage = payload.nextPage;
            break;
          case "news":
            state.newsPage = payload.nextPage;
            break;
        }
      }
    },
    bookMarkNews(state, { payload }: PayloadAction<number>) {
      if (state.state === "Loaded") {
        state.news = state.news.map((el) => (el.id === payload ? { ...el, bookmarked: !el.bookmarked } : el));
      }
    },
  },
  extraReducers: (builder) => {
    builder.addCase(fetchNews.pending, (state, action) => {
      state.state = "Loading";
    });
    builder.addCase(fetchNews.fulfilled, (state, { payload }) => {
      state.state = "Loaded";
      if (state.state === "Loaded") {
        state.news = payload.map((el) => ({ ...el, bookmarked: false }));
        state.bookmarksPage = 1;
        state.newsPage = 1;
      }
    });
    builder.addCase(fetchNews.rejected, (state, { payload }) => {
      state.state = "Error";
    });
  },
});

export const { changePage, bookMarkNews } = newsSlice.actions;
