import React, { FC, useCallback, useEffect, useMemo } from "react";
import { TabValues } from "./containers/header/types";
import { News } from "./news";
import { bookMarkNews, changePage, NewsState } from "./containers/ListoOfCards/listOfCardsSlice";
import { useAppDispatch } from "../store/hooks";
import { getLastNews } from "./containers/ListoOfCards/utils";

export const Pages: FC<{
  currentPage: TabValues;
  loadedState: Extract<NewsState, { state: "Loaded" }>;
  searchValue: string;
}> = ({ currentPage, loadedState, searchValue }) => {
  const dispatch = useAppDispatch();
  const handleBookMark = useCallback((newsId: number) => dispatch(bookMarkNews(newsId)), [dispatch]);
  const handleNextPage = useCallback((nextPage: number) => dispatch(changePage({ nextPage, list: currentPage })), [
    currentPage,
    dispatch,
  ]);

  const lastNews = useMemo(() => getLastNews(loadedState.news), [loadedState.news]);

  const newsList = useMemo(
    () =>
      loadedState.news.filter(
        (el) =>
          !searchValue ||
          el.headline.toLowerCase().includes(searchValue.toLowerCase()) ||
          el.summary.toLowerCase().includes(searchValue.toLowerCase()),
      ),
    [loadedState.news, searchValue],
  );

  useEffect(() => {
    handleNextPage(1);
  }, [handleNextPage, searchValue]);
  return (
    <>
      {currentPage === "news" && (
        <News
          lastNews={lastNews}
          currentPage={loadedState.newsPage}
          handleNextPage={handleNextPage}
          news={newsList}
          onBookmark={handleBookMark}
        />
      )}
      {currentPage === "bookmarks" && (
        <News
          lastNews={lastNews}
          currentPage={loadedState.bookmarksPage}
          handleNextPage={handleNextPage}
          news={newsList.filter((el) => el.bookmarked)}
          onBookmark={handleBookMark}
        />
      )}
    </>
  );
};
