import { createStyles, makeStyles, Typography } from "@material-ui/core";
import React, { FC } from "react";
import clsx from "clsx";

const useStyles = makeStyles(({ palette }) =>
  createStyles({
    root: {
      cursor: "pointer",
      marginRight: "20px",
      fontSize: "28px",
      fontWeight: "bold",
      color: palette.common.white,
    },
    active: {
      opacity: 1,
    },
    inActive: {
      opacity: 0.5,
    },
  }),
);

type Props = { isActive: boolean; onClick: () => void; label: string };
export const Tab: FC<Props> = ({ label, onClick, isActive }) => {
  const classes = useStyles();
  return (
    <Typography className={clsx(classes.root, isActive ? classes.active : classes.inActive)} onClick={onClick}>
      {label}
    </Typography>
  );
};
