import React, { FC } from "react";
import { Box, Button, Link, Paper, Typography } from "@material-ui/core";
import { NewsEntity } from "../../../../../types";
import moment from "moment";
import clsx from "clsx";
import TrendingUpIcon from "@material-ui/icons/TrendingUp";
import BookmarkBorderIcon from "@material-ui/icons/BookmarkBorder";
import TurnedInIcon from "@material-ui/icons/TurnedIn";
import { useStyles } from "./styles";

export const Card: FC<{ card: NewsEntity; latest?: boolean; onBookMark: () => void }> = ({
  onBookMark,
  card: { url, summary, source, related, image, datetime, bookmarked, headline },
  latest,
}) => {
  const classes = useStyles({ image, size: latest ? "big" : "normal" });
  return (
    <Paper className={classes.root} style={{ backgroundImage: image }}>
      <Box className={classes.header}>
        <Button className={classes.related} color={"secondary"} variant={"outlined"}>
          {related}
        </Button>
        {latest && (
          <Box display={"flex"} color={"white"} fontWeight={700}>
            <Box className={classes.latestLabel}>LATEST RESEARCH</Box>
          </Box>
        )}
      </Box>

      <Box className={classes.contentBox}>
        <Box className={classes.textContent}>
          <Box>
            <Typography variant={"h4"}>{headline}</Typography>
          </Box>
          <Box>
            <Typography variant={"h5"}>{summary}</Typography>
          </Box>
        </Box>

        <Box className={classes.footer}>
          <Box display={"flex"}>
            <Typography variant={"caption"} className={classes.footerItem}>
              {moment(datetime).format("D MMM")}
            </Typography>
            <Box className={clsx(classes.verticalLine, classes.footerItem)} />
            <Typography variant={"caption"} className={classes.footerItem}>
              6 min read
            </Typography>
          </Box>
          <Box display={"flex"}>
            <Link href={url} target={"_blank"} className={classes.icons}>
              <TrendingUpIcon />
            </Link>
            {bookmarked ? (
              <TurnedInIcon onClick={onBookMark} className={classes.icons} />
            ) : (
              <BookmarkBorderIcon onClick={onBookMark} className={classes.icons} />
            )}
          </Box>
        </Box>
      </Box>
    </Paper>
  );
};
