import React, { FC } from "react";
import { NewsEntity } from "../../../types";
import { Card } from "./containers/Card";
import { Grid } from "@material-ui/core";

export const LitOfCards: FC<{
  cards: Array<NewsEntity>;
  lastNews: NewsEntity;
  onBookmark: (newsId: number) => void;
}> = ({ cards, lastNews, onBookmark }) => {
  return (
    <Grid container spacing={2}>
      <Grid item xs={4}>
        <Card onBookMark={() => onBookmark(lastNews.id)} card={lastNews} latest />
      </Grid>
      <Grid item xs={8}>
        <Grid container spacing={2}>
          {cards.map((card) => (
            <Grid item xs={4}>
              <Card card={card} onBookMark={() => onBookmark(card.id)} />
            </Grid>
          ))}
        </Grid>
      </Grid>
    </Grid>
  );
};
