import React, { FC } from "react";
import { Tabs } from "../../components/Tabs";
import { useAppDispatch, useAppSelector } from "../../../store/hooks";
import { changeTab } from "./headerSlice";
import { Box } from "@material-ui/core";
import { SearchField } from "./components/SearchFiled";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      justifyContent: "space-between",
    },
  }),
);

export const Header: FC<{ onSearch: (searchValue: string) => void }> = ({ onSearch }) => {
  const classes = useStyles();
  const { selectedTab, tabs } = useAppSelector((state) => state.header);
  const dispatch = useAppDispatch();
  return (
    <Box className={classes.root}>
      <Tabs tabs={tabs} selectedTab={selectedTab} onChangeTab={(nextTab) => dispatch(changeTab(nextTab))} />
      <SearchField onChange={(e) => onSearch(e.target.value)} />
    </Box>
  );
};
