import React, { FC } from "react";
import { Box, Button, createStyles, makeStyles, Typography } from "@material-ui/core";

const useStyles = makeStyles(({ palette }) =>
  createStyles({
    root: {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
    },
    navButtons: {
      display: "flex",
    },
    navButton: {
      minWidth: "116px",
    },
  }),
);

export const NewsPagination: FC<{
  from: number;
  to: number;
  count: number;
  onNext: () => void;
  onPrev: () => void;
}> = ({ to, from, count, onNext, onPrev }) => {
  const classes = useStyles();
  return (
    <Box className={classes.root}>
      <Typography>{`${from}-${to} out of ${count}`}</Typography>
      <Box className={classes.navButtons}>
        {from !== 1 && (
          <Button className={classes.navButton} variant={"contained"} color={"primary"} onClick={() => onPrev()}>
            Prevision
          </Button>
        )}
        {to < count && (
          <Button
            style={{ marginLeft: "10px" }}
            className={classes.navButton}
            variant={"contained"}
            color={"primary"}
            onClick={() => onNext()}
          >
            Next
          </Button>
        )}
      </Box>
    </Box>
  );
};
