import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { TabProps, TabValues } from "./types";

type State = { tabs: Array<TabProps>; selectedTab: TabValues };
const initialState: State = {
  tabs: [
    { value: "news", label: "News" },
    { value: "bookmarks", label: "Bookmarks" },
  ],
  selectedTab: "news",
};

export const headerSlice = createSlice({
  name: "header",
  initialState,
  reducers: {
    changeTab: (state: State, { payload }: PayloadAction<TabValues>) => {
      state.selectedTab = payload;
    },
  },
});

export const { changeTab } = headerSlice.actions;
