import React, { FC } from "react";
import { Box, createStyles, makeStyles } from "@material-ui/core";
import { Tab } from "./Tab";
import { TabProps, TabValues } from "../../containers/header/types";

type Props = {
  tabs: Array<TabProps & { value: string }>;
  selectedTab: string;
  onChangeTab: (newTab: TabValues) => void;
};

const useStyles = makeStyles(() =>
  createStyles({
    root: {
      display: "flex",
    },
  }),
);

export const Tabs: FC<Props> = ({ tabs, onChangeTab, selectedTab }) => {
  const classes = useStyles();
  return (
    <Box className={classes.root}>
      {tabs.map(({ label, value }) => (
        <Tab key={label} label={label} onClick={() => onChangeTab(value)} isActive={value === selectedTab} />
      ))}
    </Box>
  );
};
