import moment from "moment";
import { NewsEntity } from "../../../types";

export const getElementsByPage = <T extends Array<unknown>>(arr: T, page: number, elementsPerPage: number): T => {
  if (page === 1) {
    return arr.slice(0, elementsPerPage) as T;
  }
  return arr.slice((page - 1) * elementsPerPage, page * elementsPerPage) as T;
};

export const getLastNews = (news: Array<NewsEntity>): NewsEntity =>
  news.slice().sort((a, b) => (moment(a.datetime).isAfter(b.datetime) ? 1 : -1))[0];
